body {
  color: #212529;
  font-family: Open Sans, sans-serif;
}

a {
  color: #138035;
  text-decoration: none;
}

a:hover {
  color: #1aac47;
  text-decoration: none;
}

h1, h2, h3, h4, h5, h6, .font-primary {
  font-family: Raleway, sans-serif;
}

#main {
  margin-top: 100px;
  padding: 20px 30px;
  transition: all .3s;
}

@media (width <= 1199px) {
  #main {
    padding: 20px;
  }
}

.pagetitle {
  margin-bottom: 10px;
}

.pagetitle h1 {
  color: #426f9f;
  margin-bottom: 0;
  font-size: 24px;
  font-weight: 600;
}

.back-to-top {
  visibility: hidden;
  opacity: 0;
  z-index: 99999;
  background: #138035;
  border-radius: 4px;
  width: 40px;
  height: 40px;
  transition: all .4s;
  position: fixed;
  bottom: 15px;
  right: 15px;
}

.back-to-top i {
  color: #fff;
  font-size: 24px;
  line-height: 0;
}

.back-to-top:hover {
  color: #fff;
  background: #18a444;
}

.back-to-top.active {
  visibility: visible;
  opacity: 1;
}

@media screen and (width <= 768px) {
  [data-aos-delay] {
    transition-delay: 0 !important;
  }
}

#header {
  z-index: 997;
  background: #fff;
  border-bottom: 1px solid #56b766;
  height: 220px;
  transition: all .5s;
}

#header .logo h1 {
  letter-spacing: 2px;
  margin: 0;
  font-size: 30px;
  font-weight: 400;
  line-height: 1;
}

#header .logo h1 a, #header .logo h1 a:hover {
  color: #bdbec0;
  text-decoration: none;
}

#header .logo img {
  max-height: 184px;
  margin: 0;
  padding: 0;
}

#header_nonav {
  z-index: 997;
  background: #afddb7;
  padding: 20px 0;
  transition: all .5s;
}

#header_nonav.header-transparent {
  background: none;
}

#header_nonav.header-scrolled {
  background: #afddb7;
  padding: 12px;
}

#header_nonav .toggle-sidebar-btn {
  cursor: pointer;
  color: #426f9f;
  padding-left: 10px;
  font-size: 32px;
}

#header_nonav .logo {
  max-width: 95%;
}

#header_nonav .logo .logo-text {
  color: #426f9f;
}

#header_nonav .logo h1 {
  color: #426f9f;
  letter-spacing: 1px;
  text-transform: uppercase;
  margin: 0;
  font-family: Raleway, sans-serif;
  font-size: 24px;
  font-weight: 700;
  line-height: 1;
}

#header_nonav .logo h1 a, #header_nonav .logo h1 a:hover {
  color: #426f9f;
  text-decoration: none;
}

#header_nonav .logo img {
  max-width: 350px;
  max-height: 60px;
  margin: 0;
  padding: 0;
}

@media (width <= 768px) {
  #header_nonav.header-scrolled {
    padding: 15px 0;
  }
}

@media (width <= 500px) {
  #header_nonav .logo img {
    max-width: 300px;
  }
}

.logo {
  line-height: 1;
}

.logo img {
  max-height: 26px;
  margin-right: 6px;
}

.logo span {
  color: #bdbec0;
  font-family: Raleway, sans-serif;
  font-size: 26px;
  font-weight: 700;
}

.header {
  z-index: 997;
  background-color: #fff;
  height: 100px;
  padding-left: 20px;
  transition: all .5s;
  box-shadow: 0 2px 20px #bdbec01a;
}

.header .toggle-sidebar-btn {
  cursor: pointer;
  color: #bdbec0;
  padding-left: 10px;
  font-size: 32px;
}

.header .search-bar {
  min-width: 360px;
  padding: 0 20px;
}

@media (width <= 1199px) {
  .header .search-bar {
    z-index: 9999;
    visibility: hidden;
    opacity: 0;
    background: #c4f6d4;
    padding: 20px;
    transition: all .3s;
    position: fixed;
    top: 50px;
    left: 0;
    right: 0;
    box-shadow: 0 0 15px #bdbec01a;
  }

  .header .search-bar-show {
    visibility: visible;
    opacity: 1;
    top: 60px;
  }
}

.header .search-form {
  width: 100%;
}

.header .search-form input {
  color: #bdbec0;
  border: 1px solid #bdbec033;
  border-radius: 3px;
  width: 100%;
  padding: 7px 38px 7px 8px;
  font-size: 14px;
  transition: all .3s;
}

.header .search-form input:focus, .header .search-form input:hover {
  border: 1px solid #bdbec04d;
  outline: none;
  box-shadow: 0 0 10px #bdbec026;
}

.header .search-form button {
  background: none;
  border: 0;
  margin-left: -30px;
  padding: 0;
}

.header .search-form button i {
  color: #bdbec0;
}

.navbar {
  padding: 0;
}

.navbar ul {
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.navbar li {
  position: relative;
}

.navbar a, .navbar a:focus {
  color: #88898b;
  white-space: nowrap;
  justify-content: space-between;
  align-items: center;
  padding: 10px 0 10px 30px;
  font-size: 15px;
  transition: all .3s;
  display: flex;
}

.navbar a i, .navbar a:focus i {
  margin-left: 5px;
  font-size: 12px;
  line-height: 0;
}

.navbar a:hover, .navbar .active, .navbar .active:focus, .navbar li:hover > a {
  color: #138035;
}

.navbar .getstarted, .navbar .getstarted:focus {
  color: #138035;
  background: #fff;
  border: 1px solid #138035;
  border-radius: 12px;
  margin-left: 30px;
  padding: 10px 25px;
}

.navbar .getstarted:hover, .navbar .getstarted:focus:hover {
  color: #fff;
  background: #18a444;
}

.navbar .dropdown ul {
  z-index: 99;
  opacity: 0;
  visibility: hidden;
  background: #fff;
  margin: 0;
  padding: 10px 0;
  transition: all .3s;
  display: block;
  position: absolute;
  top: calc(100% + 30px);
  left: 14px;
  box-shadow: 0 0 30px #7f89a140;
}

.navbar .dropdown ul li {
  min-width: 200px;
}

.navbar .dropdown ul a {
  text-transform: none;
  padding: 10px 20px;
  font-size: 15px;
}

.navbar .dropdown ul a i {
  font-size: 12px;
}

.navbar .dropdown ul a:hover, .navbar .dropdown ul .active:hover, .navbar .dropdown ul li:hover > a {
  color: #138035;
}

.navbar .dropdown:hover > ul {
  opacity: 1;
  visibility: visible;
  top: 100%;
}

.navbar .dropdown .dropdown ul {
  visibility: hidden;
  top: 0;
  left: calc(100% - 30px);
}

.navbar .dropdown .dropdown:hover > ul {
  opacity: 1;
  visibility: visible;
  top: 0;
  left: 100%;
}

@media (width <= 1366px) {
  .navbar .dropdown .dropdown ul {
    left: -90%;
  }

  .navbar .dropdown .dropdown:hover > ul {
    left: -100%;
  }
}

.mobile-nav-toggle {
  color: #bdbec0;
  cursor: pointer;
  font-size: 28px;
  line-height: 0;
  transition: all .5s;
  display: none;
}

.mobile-nav-toggle.bi-x {
  color: #fff;
}

@media (width <= 991px) {
  .mobile-nav-toggle {
    display: block;
  }

  .navbar ul {
    display: none;
  }
}

.navbar-mobile {
  background: #88898be6;
  transition: all .3s;
  position: fixed;
  inset: 0;
  overflow: hidden;
}

.navbar-mobile .mobile-nav-toggle {
  position: absolute;
  top: 15px;
  right: 15px;
}

.navbar-mobile ul {
  background-color: #fff;
  padding: 10px 0;
  transition: all .3s;
  display: block;
  position: absolute;
  inset: 55px 15px 15px;
  overflow-y: auto;
}

.navbar-mobile a, .navbar-mobile a:focus {
  color: #bdbec0;
  padding: 10px 20px;
  font-size: 15px;
}

.navbar-mobile a:hover, .navbar-mobile .active, .navbar-mobile li:hover > a {
  color: #138035;
}

.navbar-mobile .getstarted, .navbar-mobile .getstarted:focus {
  margin: 15px;
}

.navbar-mobile .dropdown ul {
  z-index: 99;
  opacity: 1;
  visibility: visible;
  background: #fff;
  margin: 10px 20px;
  padding: 10px 0;
  display: none;
  position: static;
  box-shadow: 0 0 30px #7f89a140;
}

.navbar-mobile .dropdown ul li {
  min-width: 200px;
}

.navbar-mobile .dropdown ul a {
  padding: 10px 20px;
}

.navbar-mobile .dropdown ul a i {
  font-size: 12px;
}

.navbar-mobile .dropdown ul a:hover, .navbar-mobile .dropdown ul .active:hover, .navbar-mobile .dropdown ul li:hover > a {
  color: #138035;
}

.navbar-mobile .dropdown > .dropdown-active {
  display: block;
}

#hero {
  background-color: #f1f1f2;
  border-bottom: 1px solid #56b766;
  width: 100%;
  height: 70vh;
  margin: 220px 0 0;
}

#hero h1 {
  color: #343a40;
  margin: 0;
  font-size: 48px;
  font-weight: 700;
  line-height: 56px;
}

#hero h2 {
  color: #1aac47;
  margin: 15px 0 0;
  font-size: 24px;
}

#hero .btn-get-started {
  letter-spacing: 1px;
  color: #138035;
  background: #fff;
  border: 1px solid #138035;
  border-radius: 12px;
  margin-top: 30px;
  padding: 8px 28px;
  font-family: Raleway, sans-serif;
  font-size: 16px;
  font-weight: 500;
  transition: all .5s;
  display: inline-block;
}

#hero .btn-get-started:hover {
  color: #fff;
  background: #1aac47;
}

#hero .animated {
  animation: 2s ease-in-out infinite alternate-reverse both up-down;
}

@media (width <= 991px) {
  #hero {
    height: calc(100vh - 72px);
  }

  #hero .animated {
    animation: none;
  }

  #hero .hero-img {
    text-align: center;
  }

  #hero .hero-img img {
    width: 50%;
  }
}

@media (width <= 768px) {
  #hero h1 {
    font-size: 28px;
    line-height: 36px;
  }

  #hero h2 {
    font-size: 18px;
    line-height: 24px;
  }

  #hero .hero-img img {
    width: 70%;
  }
}

@media (width <= 575px) {
  #hero .hero-img img {
    width: 80%;
  }
}

@media (height <= 600px) {
  #hero {
    height: 120vh;
  }
}

@keyframes up-down {
  0% {
    transform: translateY(10px);
  }

  100% {
    transform: translateY(-10px);
  }
}

section {
  padding: 60px 0;
  overflow: hidden;
}

.section-bg {
  background: #f1f1f2;
}

.section-bg-grn {
