}

.section-title {
  text-align: center;
  padding-bottom: 30px;
}

.section-title h2 {
  color: #bdbec0;
  margin-bottom: 20px;
  padding-bottom: 20px;
  font-size: 32px;
  font-weight: bold;
  position: relative;
}

.section-title h2:before {
  content: "";
  background: #ddd;
  width: 120px;
  height: 1px;
  display: block;
  position: absolute;
  bottom: 1px;
  left: calc(50% - 60px);
}

.section-title h2:after {
  content: "";
  background: #138035;
  width: 40px;
  height: 3px;
  display: block;
  position: absolute;
  bottom: 0;
  left: calc(50% - 20px);
}

.section-title h3 {
  color: #343a40;
  font-size: 34px;
  font-weight: 700;
}

.section-title p {
